import '@/common/linq';
import auth from '@/common/auth';
export default {
	data() {
		return {
			selectDate: [],
			day: 0,
			month: 0,
			year: 0,
			selectTap: 0,
			bookedFoods: [],
			selectD: null
		};
	},
	watch: {
		selectD(v) {
			this.getMealList(v);
		}
	},
	onShow() {
		uni.getNetworkType({
			success: function(res) {
				console.log(res.networkType);
				if (res.networkType == 'none') {
					uni.reLaunch({
						url: '/pages/notNetwork/notNetwork'
					});
				}
			}
		});
	},
	onLoad() {
		let t = this;

		t.$api
			.GetUseFullDateListAsync({
				isShowNullDate: false
			})
			.then(res => {
				console.log(res);
				if (res.data.length > 0) {
					new Promise((resolve, reject) => {
						res.data.forEach(item => {
							// console.log(item)
							// debugger
							var y = item.menuDate.split('T')[0].split('-')[0];
							var m = item.menuDate.split('T')[0].split('-')[1];
							var d = item.menuDate.split('T')[0].split('-')[2];
							t.selectDate.push({
								week: auth.judgeWeek(y, m, d),
								month: m + '-' + d,
								y: y
							});
						});
						resolve(res.data[0].menuDate);
					}).then(data => {
						this.selectD = data;
						this.getMealList(data);
					});
				}
			})
			.catch(err => {});
	},
	methods: {
		getMealList(data) {
			this.$api
				.GetBookMealListAsync({
					Date: data
				})
				.then(res => {
					this.bookedFoods = res.data;
				})
				.catch(err => {});
		},
		selectDateList(i, m, y) {
			this.selectTap = i;
			var data = y + '-' + m;
			this.selectD = data;
		},
		// 判断传入日期是星期几
		judgeOneWeek(y, m, d) {
			var data = parseInt(new Date(new Date(y + '/' + m + '/' + d).getTime()).getDay());
			if (y - this.year == 0) {
				// return '今年'
				if (m - this.month == 0) {
					if (d - this.day == 1) {
						return '明天';
					} else if (d - this.day == 2) {
						return '后天';
					} else {
						switch (data) {
							case 1:
								return '周一';
								break;
							case 2:
								return '周二';
								break;
							case 3:
								return '周三';
								break;
							case 4:
								return '周四';
								break;
							case 5:
								return '周五';
								break;
							case 6:
								return '周六';
								break;
							case 0:
								return '周天';
								break;
						}
					}
				} else {
					switch (data) {
						case 1:
							return '周一';
							break;
						case 2:
							return '周二';
							break;
						case 3:
							return '周三';
							break;
						case 4:
							return '周四';
							break;
						case 5:
							return '周五';
							break;
						case 6:
							return '周六';
							break;
						case 0:
							return '周天';
							break;
					}
				}
			}
		}
	}
};
